import { App } from '../../App.js'
import ComponentConst from '../../const/component'
import CONST_SOCKET from '../../const/socket'

// eslint-disable-next-line
export default async (component, event, ui) => {
  App.editor.view.area.pointermove(event)
  const { x, y } = App.editor.view.area.mouse
  const genericTemplateNode = await component.createNode()
  genericTemplateNode.position = [x, y]

  App.editor.addNode(genericTemplateNode)
  
  const buttonNode = await createButtonNode()
  buttonNode.position = [x + 250, y]

  App.editor.addNode(buttonNode)

  App.editor.connect(
    genericTemplateNode.outputs.get(CONST_SOCKET.GENERIC_TEMPLATE_OUTPUT_BUTTON),
    buttonNode.inputs.get(CONST_SOCKET.BUTTON_INPUT)
  )
}

const createButtonNode = async () => {
  const buttonComponent = App.components.get(ComponentConst.BUTTON)
  const buttonInstance = new buttonComponent(App.config)
  const buttonNode = await buttonInstance.createNode()

  return buttonNode
}