import { fileModal } from './fileModal'
import { updateNodeConnections } from '../../functions/utils'

// eslint-disable-next-line no-unused-vars
export default async (node, editor) => {
  const oldData = node.data
  const { value: fileData } = await fileModal(oldData, node)
  
  if (fileData && Object.values(fileData).length >= 1) {
    Object.assign(node.data, fileData)
    node.update()
    updateNodeConnections(node)
  }


  return false
}