import { App } from '../../App.js'
import Const from '../../const/component'
import CONST_SOCKET from '../../const/socket'
import { fixYPositionOfChildComponents } from '../../functions/utils'

// eslint-disable-next-line
export default async (component, event, ui) => {
  App.editor.view.area.pointermove(event)
  const { x, y } = App.editor.view.area.mouse
  const carouselNode = await component.createNode()
  carouselNode.position = [x, y]

  App.editor.addNode(carouselNode)

  const buttonComponent = App.components.get(Const.BUTTON)
  const carouselSingleComponent = App.components.get(Const.CAROUSEL_ITEM)
  
  const carouselSingles = [
    new carouselSingleComponent(App.config),
    new carouselSingleComponent(App.config),
    new carouselSingleComponent(App.config),
  ]
  
  carouselSingles.map(async (carouselSingle, index) => {
    const carouselSingleNode = await carouselSingle.createNode()
    const buttonInstance = new buttonComponent(App.config)
    const buttonNode = await buttonInstance.createNode()
    
    const carouselSingleY = fixYPositionOfChildComponents(index, 90, carouselSingles.length)
    carouselSingleNode.position = [x + 250, carouselSingleY]

    const buttonY = fixYPositionOfChildComponents(index, 130, carouselSingles.length)
    buttonNode.position = [x + 480, buttonY]
    
    App.editor.addNode(carouselSingleNode)
    App.editor.addNode(buttonNode)

    App.editor.connect(
      carouselNode.outputs.get(CONST_SOCKET.CAROUSEL_OUTPUT_ITEMS), 
      carouselSingleNode.inputs.get(CONST_SOCKET.CAROUSEL_SINGLE_INPUT)
    )
    
    App.editor.connect(
      carouselSingleNode.outputs.get(CONST_SOCKET.CAROUSEL_SINGLE_OUTPUT),
      buttonNode.inputs.get(CONST_SOCKET.BUTTON_INPUT)
    )
  })
}