import { App } from '../../App.js'
import CONST_SOCKET from '../../const/socket'
import {
  addNewPostbackNodeToEditor,
  addNewSequenceSingleToEditor,
  fixYPositionOfChildComponents,
} from '../../functions/utils'

// eslint-disable-next-line
export default async (component, event, ui) => {
  App.editor.view.area.pointermove(event)
  const { x, y } = App.editor.view.area.mouse
  const newSequenceNode = await component.createNode()
  newSequenceNode.position = [x, y]

  App.editor.addNode(newSequenceNode)

  const items = [1, 2, 3]
  
  // eslint-disable-next-line
  items.map(async (item, index) => { 
    const sequenceSingleNodePositionY = fixYPositionOfChildComponents(index, 100, items.length)
    const squenceSingleNodePosition = [x + 250, sequenceSingleNodePositionY]
    const sequenceSingleNode = await addNewSequenceSingleToEditor(squenceSingleNodePosition)

    const childNewPostbackNodePositionY = fixYPositionOfChildComponents(index, 122, items.length)
    const childNewPostbackNodePosition = [x + 500, childNewPostbackNodePositionY]
    const childNewPostbackNode = await addNewPostbackNodeToEditor(childNewPostbackNodePosition)

    App.editor.connect(
      newSequenceNode.outputs.get(CONST_SOCKET.SEQUENCE_OUTPUT),
      sequenceSingleNode.inputs.get(CONST_SOCKET.SEQUENCE_SINGLE_INPUT)
    )

    App.editor.connect(
      sequenceSingleNode.outputs.get(CONST_SOCKET.SEQUENCE_SINGLE_OUTPUT),
      childNewPostbackNode.inputs.get(CONST_SOCKET.NEW_POSTBACK_INPUT)
    )
  })
}