<template>
  <div id="xit-flow-builder" class="xit-flow-builder">
    <div
      ref="xitFlowBuilder"
      id="xit-fb-droppable"
    ></div>
    
    <action-buttons />
    <dock-menu-card :faviconSrc="getFavicon()" />
    <hover-node-card />

  </div>
</template>

<script>
import { App } from '../App.js'
import moduleConfigs from '../config/modules'

import DockMenuCard from './dock/DockMenuCard'
import ActionButtons from './editor/ActionButtons'
import HoverNodeCard from './editor/HoverNodeCard'
import ComponentsName from '../const/component'
import {
  alertBeforeClosingBrowser,
} from '../functions/utils'

export default {
  name: 'Flow',
  components: {
    'dock-menu-card': DockMenuCard,
    'action-buttons': ActionButtons,
    'hover-node-card': HoverNodeCard,
  },

  methods: {
    getFavicon() {
      return window.xitFlowBuilderData.base_url 
        ? window.xitFlowBuilderData.base_url + 'assets/img/favicon.png'
        : null
    }
  },
  
  mounted() {
    window.hasXitFlowBuilderData = false
    var data = window.xitFlowBuilderData.data || null
    var builder_id = window.xitFlowBuilderData.builder_id || null
    
    // Inits application
    const app = new App(this.$refs.xitFlowBuilder, moduleConfigs, builder_id)

    if (data) {
      app.fromJSON(data)
    } else {
      app.setDefaultComponent(ComponentsName.POSTBACK, [-450, -50])
    }

    // Makes flow builder area droppable
    $('#xit-fb-droppable').droppable({
      tolerance: "pointer",
      drop: async function(event, ui) {
        await App.handleComponentCreate(event, ui)
      },
    })

    // Inits bootstrap tooltip
    $('[data-toggle="tooltip"]').tooltip()

    if (! App.isDevMode) {
      alertBeforeClosingBrowser()
    }
  },

  beforeDestroy() { // eslint-disable-line
    const xitFbDroppableElement = $('#xit-fb-droppable')
    const isxitFbDroppableDroppable = xitFbDroppableElement.hasClass('ui-droppable')
    
    if (isxitFbDroppableDroppable) {
      xitFbDroppableElement.droppable('destroy')
    }

    const tooltip = $('[data-toggle="tooltip"]')
    if (tooltip) {
      tooltip.tooltip('dispose')
    }
  }
}
</script>

<style>
.xit-flow-builder {
  /* position: relative;
  min-height: 100%; */
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  height: 100vh;
  background: white; 
}
</style>