<template>
  <div 
    class="xit-fb-template node"
    @dblclick.stop="handleClick(node)" 
    :class="[camelCase(node.name), hasData(node), isSelected(node)]" 
  >
    <div class="title">
      <p class="title-content"><i class="fas fa-cart-plus card-file-icon"></i> {{node.lingualName}}</p>
    </div>

    <div class="card-body-handler card_data_body p-0">
      <div class="card-data">
        <div v-if="isStoreSelected(node) && isProductSelected(node)">

          <div class="typing_delay clearfix mb-2 p-1 pl-3 pr-3" v-if="node.data.IsTypingOnDisplayChecked">
            <p class="typing_display float-left mb-0 p-0">
              <img :src="baseUrl + 'assets/pre-loader/typing_on.gif'" height="9px" width="20px"> {{ typing_delay }}
            </p>
            <p class="delay_time float-right mb-0" v-if="node.data.delayReplyFor">
              {{ node.data.delayReplyFor }} {{ sec }}
            </p>
          </div>

          <p class="shop_name">{{ node.data.ecommerceStoreText }}</p>
          <small class="products_list">{{ displayProductList(node) }}</small>

          <div v-if="node.data.ecommerceButtonTextValue" class="text-center">
            <p class="single_button">{{ node.data.ecommerceButtonTextValue }}</p>
          </div>

        </div>
      </div>

      <div class="card-sockets">
        <div class="output_content">
          <div class="output text-right" v-for="output in outputs()" :key="output.key">
            <div class="output-title">{{output.name}}</div>
            <Socket v-socket:output="output" type="output" :socket="output.socket"></Socket>
          </div>
        </div>
        <div class="input_content">
          <div class="input text-left" v-for="input in inputs()" :key="input.key">
            <Socket v-socket:input="input" type="input" :socket="input.socket"></Socket>
            <div class="input-title">{{input.name}}</div>
          </div>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script>
import VueRenderPlugin from 'rete-vue-render-plugin'
import { lang } from '../../assets/lang'
import { templateComponentMethods } from '../../functions/utils'

export default {
  name: 'EcommerceTemplate',
  mixins: [VueRenderPlugin.mixin],
  components: {
    Socket: VueRenderPlugin.Socket
  },

  data: () => ({
    sec: lang.common_info_sec,
    baseUrl: window.xitFlowBuilderData.base_url,
    typing_display: lang.common_info_typing_display,
  }),

  methods: {
    ...templateComponentMethods,

    isStoreSelected(node) {
      return node.data.ecommerceStoreValue && node.data.ecommerceStoreText
    },
    
    isProductSelected(node) {
      return node.data.ecommerceProductValue && node.data.ecommerceProductText
    },

    displayProductList(node) {
      if (this.isProductSelected(node)) {
        return Array.isArray(node.data.ecommerceProductText) 
          ? node.data.ecommerceProductText.join(', ')
          : ''
      }
    },
  }
}
</script>

<style scoped>
.card-file-icon { font-size: 13px; color: #76766f; }
.typing_delay {background-color:#f6f4f4; border-radius: 7px; font-size: 14px;}
.typing_display_none {text-decoration: line-through; text-decoration-color: red; text-decoration-thickness: from-font;}
.single_button { 
  font-weight: 500;
  background-color: #D8DADF;
  padding: 3px 20px 5px 20px;
  border-radius: 7px;
  text-transform: capitalize;
  font-size: 11px;
  margin: 5px 40px;
}
.shop_name {
  margin-bottom: -8px;
  font-size: 18px;
  font-family: Garamond;
  font-weight: 900;
}
.products_list {color: #8d8d8d; }
.input_content { width:50% !important;margin-top: 25px;}
.output_content{ width:50% !important;float:right !important;}
.card-sockets { padding: 5px 0 !important;}
.xit-fb-template.hasData { min-width: 240px !important; }
</style>