export const removeNodes = (nodesList) => {
  if (Array.isArray(nodesList)) {
    for (const node of nodesList) {
      if (node instanceof Rete.Node) {
        App.editor.removeNode(node)
      }
    }
  }
}

export const fixYPositionOfChildComponents = (index, componentHeight, totalComponents) => {
  let halfOfTotal = componentHeight * totalComponents / 2
  return (componentHeight * index) - (halfOfTotal - componentHeight / 2)
}

export const isFileTypeVerified = (file, allowedTypes) => {
  if ((file instanceof File) && Array.isArray(allowedTypes)) {
    if (allowedTypes.indexOf(file.type) > -1) {
      return true
    }
  }

  return false
}

export const addClassToAll = (className, querySelector) => {
  const items = document.querySelectorAll(querySelector)

  Array.from(items).forEach((item) => {
    if (! item.classList.contains(className)) {
      item.classList.add(className)
    }
  })
}

export const removeAllClasses = (className, querySelector) => {
  const items = document.querySelectorAll(querySelector)
  
  Array.from(items).forEach((item) => {
    if (item.classList.contains(className)) {
      item.classList.remove(className)
    }
  })
}

