import Swal from 'sweetalert2'

import { App } from '../App'
import { lang } from '../assets/lang'
import CONST_SOCKET from '../const/socket'

// eslint-disable-next-line
export const printMessageForDevelopmentMode = ({ message, data }) => {
  if (App.isDevMode) {
    console.log(message)
  }

  return false
}

export const disableZoomForDoubleClick = ({ source }) => {
  return source !== 'dblclick'
}

export const doNotShowContextMenu = ({ node }) => {
  return !node || !App.editor.components.get(node.name).data.noContextMenu
}

export const doNotLetDeleteReferenceNode = (node) => {
  return !App.editor.components.get(node.name).data.canNotBeDeleted 
}

export const warnForIncompatibleConnection = ({ output, input }) => {
  const areSocketsCompatible = output.socket.compatibleWith(input.socket)

  if (false === areSocketsCompatible) {
    // Shows different message for "New Question/User input flow sinlge"
    if (output.socket.name === CONST_SOCKET.USER_INPUT_FLOW_SINGLE_OUTPUT) {
      Swal.fire({
        icon: 'warning',
        title: lang.utils_title_warning,
        text: lang.plugin_incompatible_connection_user_input_flow_builder
      })

      return false
    }

    Swal.fire({
      icon: 'warning',
      title: lang.utils_title_warning,
      text: lang.plugin_incompatible_connection
    })
  }

  return areSocketsCompatible
}

export const hideModalIfNodeRemoved = (node) => {
  if (Swal.isVisible()) {
    const className = 'node-id-' + node.id
    if ($('.swal2-container').hasClass(className)) {
      Swal.close()
    }
  }
}