import { facebookMediaModal } from './facebookMediaModal'
import { updateNodeConnections } from '../../functions/utils'

// eslint-disable-next-line no-unused-vars
export default async (node, editor) => {
  const inputValue = node.data
  const { value: facebookMediaData } = await facebookMediaModal(inputValue, node)

  if (facebookMediaData) {
    Object.assign(node.data, facebookMediaData)
    node.update()
    updateNodeConnections(node)
  }


  return false
}