import Rete from 'rete'

import CONST_SOCKET from '../../const/socket'
import lingualName from '../../const/lingualName'
import CONST_COMPONENT from '../../const/component'
import ReferenceTemplate from './ReferenceTemplate'
import { lang } from '../../assets/lang'

export default class ReferenceComponent extends Rete.Component {
  constructor (config) {
    super(CONST_COMPONENT.POSTBACK)

    this.data.noContextMenu = true 
    this.data.canNotBeDeleted = true
    this.data.component = ReferenceTemplate 
    this.socket = config ? config.sockets : null
  }

  builder (node) {
    // Sets nodes canonical name
    node.lingualName = lingualName.POSTBACK
    this.setConnection()
    
    const referenceInput = new Rete.Input(
      CONST_SOCKET.REFERENCE_INPUT, 
      lang.postback_socket_input_trigger, 
      this.socket.referenceInput, 
      false
    )

    const referenceInputActionButton = new Rete.Input(
      CONST_SOCKET.REFERENCE_INPUT_ACTION_BUTTON, 
      lang.postback_socket_input_action_button, 
      this.socket.referenceInputActionButton, 
      false
    )

    const referenceOutput = new Rete.Output(
      CONST_SOCKET.REFERENCE_OUTPUT, 
      lang.postback_socket_output_next, 
      this.socket.referenceOutput, 
      false
    )

    return node
      .addInput(referenceInput)
      .addInput(referenceInputActionButton)
      .addOutput(referenceOutput)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    outputs[CONST_SOCKET.REFERENCE_OUTPUT] = node.data
  }

  setConnection() {
    // Reference may be connected to:
    this.socket.referenceOutput.combineWith(this.socket.textInput)
    this.socket.referenceOutput.combineWith(this.socket.carouselInput)
    this.socket.referenceOutput.combineWith(this.socket.facebookMediaInput)
    this.socket.referenceOutput.combineWith(this.socket.imageInput)
    this.socket.referenceOutput.combineWith(this.socket.videoInput)
    this.socket.referenceOutput.combineWith(this.socket.audioInput)
    this.socket.referenceOutput.combineWith(this.socket.fileInput)
    this.socket.referenceOutput.combineWith(this.socket.ecommerceInput)
    this.socket.referenceOutput.combineWith(this.socket.userInputFlowInput)
    this.socket.referenceOutput.combineWith(this.socket.genericTemplateInput)
    this.socket.referenceOutput.combineWith(this.socket.otnInput)    
    this.socket.referenceOutput.combineWith(this.socket.conditionInput)    
  }
}