import { otnModal } from './otnModal'
import { updateNodeConnections } from '../../functions/utils'

// eslint-disable-next-line
export default async (node, editor) => {
  const oldData = node.data
  const { value: otnData } = await otnModal(oldData, node)

  if (otnData && Object.values(otnData).length > 0) {
    Object.assign(node.data, otnData)
    node.update()
    updateNodeConnections(node)
  }


  return false
}