import { textModal } from './textModal'
import { updateNodeConnections } from '../../functions/utils'

// eslint-disable-next-line no-unused-vars
export default async (node, editor) => {
  const oldData = node.data
  const { value: textData } = await textModal(oldData, node)

  if (textData && textData.textMessage.length > 0) {
    Object.assign(node.data, textData)
    node.update()
    updateNodeConnections(node)
  }


  return false
}